import styled from "styled-components";
import { devices } from "../../../Breakpoints/Breakpoints";

const AboutContainer = styled.div`
    border-bottom: 1px solid var(--color-text-tertiary);
    padding: 50px 0 100px 0;

    @media ${devices.mobile}{
        padding: 30px 0 50px 0;
    }
`;

const AboutTitle = styled.h1`
    font-size: 55px;
    width: max-content;
    margin-bottom: 40px;

    &:focus{
        outline: 1px solid var(--color-accent);
    }

    @media ${devices.tablet}{
        font-size: 50px;   
    }

    @media ${devices.mobile}{
        font-size: 35px;
        margin-bottom: 25px;
    }
`;

const AboutContent = styled.div`
    font-size: 18px;
    color: var(--color-text-secondary);
    display: flex;

    @media ${devices.tablet}{
        flex-direction: column;
    }

    @media ${devices.mobile}{
        font-size: 16px;   
    }
`;

const ContentLeft = styled.div`
    flex-basis: 40%;
`;

const Description = styled.p`
    line-height: 1.5;

    @media ${devices.tablet}{
        margin-bottom: 15px;
    }
`;

const ContentRight = styled.div`
    flex-basis: 60%;
    display: flex;
    justify-content: flex-end;
    margin-right: 55px;

    @media ${devices.laptop}{
        margin-right: 0;
    }

    @media ${devices.tablet}{
        justify-content: initial;
    }
`;

const PersonalInformation = styled.ul``;

const PersonalInformationItem = styled.li`
    &:not(:last-child) {
        margin-bottom: 5px;
    }
`;

export { AboutContainer, AboutTitle, AboutContent, ContentLeft, Description, ContentRight, PersonalInformation, PersonalInformationItem };