import React from 'react';
import { useSettingsContext } from "../../../Contexts/SettingsContext";
import { AboutContainer, AboutTitle, AboutContent, ContentLeft, Description, ContentRight, PersonalInformation, PersonalInformationItem } from "./About.style";

const multiLanguageContent = {
    en: {
        title: "About me",
        description: "I'm a Front-end Developer with a passion for learning new technologies and I love programming beautiful, user-friendly websites.",
        age: "Age: 21",
        phone: "Phone: +84 818 264 017",
    },
    vn: {
        title: "Giới thiệu",
        description: "Mình là Front-end Developer có niềm đam mê học hỏi những công nghệ mới, lập trình những trang web thân thiện với người dùng.",
        age: "Tuổi: 21",
        phone: "Điện thoại: +84 818 264 017",
    }
};

function About() {
    const { settings: { language } } = useSettingsContext();
    const { title, description, age, phone } = multiLanguageContent[language];

    return (
        <AboutContainer>
            <AboutTitle tabIndex={0}>{title}</AboutTitle>
            <AboutContent>
                <ContentLeft>
                    <Description>{description}</Description>
                </ContentLeft>
                <ContentRight>
                    <PersonalInformation>
                        <PersonalInformationItem>{age}</PersonalInformationItem>
                        <PersonalInformationItem>{phone}</PersonalInformationItem>
                    </PersonalInformation>
                </ContentRight>
            </AboutContent>
        </AboutContainer>
    );
}

export default About;