import React, { useEffect, useState } from 'react';
import { HeaderContainer, HeaderTitle, HeaderContent, ContentLeft, Email, EmailContainer, Job, ContentRight, Content, List, ListItem, Link } from "./Header.style";
import moment from "moment";
import { tz } from "moment-timezone";
import { useSettingsContext } from "../../../Contexts/SettingsContext";
import { useNotificationsContext } from "../../../Contexts/NotificationsContext";

const formatDate = date => date.tz("Asia/Bangkok").format("hh:mm A");

const multiLanguageContent = {
    en: {
        name: "Huy",
        email: "tungochuy2001@gmail.com",
        emailTitle: "Send email",
        job: "Front-end Developer",
        address: "Ha Noi, Viet Nam",
        contact: "Get in touch",
    },
    vn: {
        name: "Huy",
        email: "tungochuy2001@gmail.com",
        emailTitle: "Gửi email",
        job: "Front-end Developer",
        address: "Hà Nội, Việt Nam",
        contact: "Liên hệ với mình",
    }
};

function Header() {
    const [currentDate, setCurrentDate] = useState(moment());
    const { settings: { language } } = useSettingsContext();
    const { notifications, setNotifications } = useNotificationsContext();

    const { name, email, emailTitle, job, address, contact } = multiLanguageContent[language];

    const handleCopyIconClick = () => {
        navigator.clipboard.writeText(email);
        const notificationId = "header-copy-email";
        if (notifications.findIndex(notification => notification.id === notificationId) === -1) {
            setNotifications(previousNotifications => [...previousNotifications, {
                id: notificationId,
                type: "success",
                message: "Email copied to clipboard",
                duration: 2000,
            }]);
        }
    };

    useEffect(() => {
        const updateCurrentDateInterval = setInterval(() => setCurrentDate(moment()), 1000);

        return () => {
            clearInterval(updateCurrentDateInterval);
        };
    }, []);


    return (
        <HeaderContainer>
            <HeaderTitle tabIndex={0}>{name}</HeaderTitle>
            <HeaderContent>
                <ContentLeft>
                    <EmailContainer>
                        <Email href="mailto:tungochuy2001@gmail.com" title={emailTitle} tabIndex={0}>{email}</Email>
                        <i className="fa-regular fa-copy" title="Copy email" tabIndex={0} onClick={handleCopyIconClick}></i>
                    </EmailContainer>
                    <Job>{job}</Job>
                </ContentLeft>
                <ContentRight>
                    <Content>{address} {formatDate(currentDate)}</Content>
                    <Content>{contact}</Content>
                    <List>
                        <ListItem tabIndex={0}><Link tabindex="0" href="https://github.com/ngochuytu">Github</Link></ListItem>
                        <ListItem tabIndex={0}><Link tabindex="0" href="../../../Assets/Documents/Home/Header/CV_TuNgocHuy.pdf" download={true}>Download CV</Link></ListItem>
                    </List>
                </ContentRight>
            </HeaderContent>
        </HeaderContainer>
    );
}

export default Header;